}

.cell {
  border-bottom: 1px dashed rgba(0,0,0,0.15);
}

.dz-message img {
  max-width: 110px;
}

.menu__list {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__item {
  display: block;
  margin: 1em 0;
}

.menu__link {
  font-size: 1.05em;
  font-weight: bold;
  display: block;
  padding: 1em;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.menu__link:hover,
.menu__link:focus {
  outline: none;
}

.menu--nav .menu__item {
  position: relative;
}

.menu--nav .menu__link {
  position: relative;
  min-width: 115px;
  height: 50px;
  padding: 1em 2em;
  border: none;
  text-align: center;
  color: #b5b5b5;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  margin: 0;
}

.menu--nav .menu__link:hover,
.menu--nav .menu__link:focus {
  color: #929292;
  background: transparent;
}

.menu--nav .menu__item--current .menu__link {
  color: $themedarkblue;
  margin: 0;
}

.menu--nav .menu__item::before,
.menu--nav .menu__item::after,
.menu--nav .menu__link::after {
  content: '';
  position: absolute;
  bottom: 0;
  background: $themedarkblue;
}

.menu--nav .menu__item::before,
.menu--nav .menu__item::after {
  width: 2px;
  height: 100%;
  opacity: 0;
  -webkit-transform: scale3d(1, 0, 1);
  transform: scale3d(1, 0, 1);
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
  -webkit-transition: -webkit-transform 0s 0.1s, opacity 0.1s;
  transition: transform 0s 0.1s, opacity 0.1s;
}

.menu--nav .menu__item::before {
  left: 0;
}

.menu--nav .menu__item::after {
  right: 0;
}

.menu--nav .menu__item--current::before,
.menu--nav .menu__item--current::after {
  opacity: 1;
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  z-index: 99;
}

/* bottom line */
.menu--nav .menu__link::after {
  left: 0;
  width: 100%;
  height: 2px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

.menu--nav .menu__item--current .menu__link::after {
  -webkit-transform: translate3d(0, -48px, 0);
  transform: translate3d(0, -48px, 0);
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

