.loading {
  opacity: .25;
  pointer-events: none;
  @include ts(opacity ease-in-out .35s);
}

.done-loaded {
  opacity: 1;
  pointer-events: all;
  @include ts(opacity ease-in-out .25s);
}

.shaky {
  @include am(shake ease-in-out .7s backwards);
}

@keyframes shake {
  from, to {
    transform: translate3d(0, 0, 0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-12px, 0, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate3d(12px, 0, 0);
  }
}

.send {
  position: relative;
  width:200px;
  height: 200px;
  margin: 0 auto;
  -webkit-filter: url("#goo");
  filter: url("../send.html#goo");
}

.send-button {
  position: absolute;
  left: 50%;
  top: 50%;
  background: #f78e42;
  border: none;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin-top: -40px;
  margin-left: -40px;
  color: #fff;
  font-size: 30px;
  padding: 0;
  overflow: hidden;
}

.sent-bg{
  background: #5af158;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.send-icon {
  position: relative;
  font-size: 28px;
  margin: -2px 0 0 -2px;
}

.sent-icon, .failed-icon {
  display: none;
}

.send-indicator {
  position: absolute;
  left: 50%;
  top: 50%;
}

.send-indicator-dot {
  position: absolute;
  background: #5af158;
  width: 40px;
  height: 40px;
  margin-left: -20px;
  margin-top: -20px;
  border-radius: 50%;
}

.send-indicator-dot:nth-child(1) {
  background: #03a9f4;
  -webkit-transform: scale(0.8, 0.8);
  transform: scale(0.8, 0.8);
}

.send-indicator-dot:nth-child(2) {
  background: #5af158;
  -webkit-transform: scale(0.85, 0.85);
  transform: scale(0.85, 0.85);
}

.send-indicator-dot:nth-child(3) {
  background: #ffc107;
  -webkit-transform: scale(0.9, 0.9);
  transform: scale(0.9, 0.9);
}

.send-indicator-dot:nth-child(4) {
  background: #ff5722;
  -webkit-transform: scale(0.7, 0.7);
  transform: scale(0.7, 0.7);
}

.js .input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.input-file + label {
  max-width: 80%;
  font-size: 1.25rem;
  font-weight: 700;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  padding: 0.625rem 1.25rem;
}

.input-file + label figure {
  font-size: 4.8em;
  color: #fff;
}

.no-js .input-file + label {
  display: none;
}

.input-file:focus + label,
.input-file.has-focus + label {
  outline: 1px dotted #000;
  outline: -webkit-focus-ring-color auto 5px;
}

.input-file + label * {
  /* pointer-events: none; */
  /* in case of FastClick lib use */
}

.input-uploader + label {
  color: $themedarkblue;
}

.input-uploader:focus + label,
.input-uploader.has-focus + label,
.input-uploader + label:hover {
  color: $themelightblue;
}

.input-uploader + label figure {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: $themedarkblue;
  display: block;
  padding: 20px;
  margin: 0 auto 10px;
}

.input-uploader:focus + label figure,
.input-uploader.has-focus + label figure,
.input-uploader + label:hover figure {
  background-color: rgba($themedarkblue, 0.65);
}

.user-avatar {
  display: inline-block;
  padding: 0;
  margin: 0 3px;
  width: 35px;
  height: 35px;
}

.btn-naked {
  &:hover {
    text-decoration: none;
    opacity: .75;
  }
}

.search-input {
  position: relative;
  padding: 5px 28px !important;
  display: block;

  & + i.fa-search:before {
    position: absolute;
    left: 25px;
    top: 10px;
    color: #bbb;
    @include ts();
  }
  
  &:focus + i.fa-search:before {
    color: $themedarkblue;
  }
}